import React, {useState} from 'react'
import { Link } from "react-router-dom";
import AppBar from '@material-ui/core/AppBar'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import { makeStyles } from '@material-ui/styles'
import "./Header.css"

const styles = makeStyles({
        AppBar: {
            background: '#222629'
        },
        Tab: {
            color: '#94b4c3',
            fontSize: 15
        }
    });

const Header = () => {
    const [current, setCurrent] = useState(() => {
        switch (window.location.pathname) {
            case "/": return 0;
            case "/match/": return 1;
            case "/player/": return 2;
            default: return 0;
        }
    });

    

    const classes = styles();
    return (
        <AppBar className={classes.AppBar} position="static">
            <Tabs  
                value={current} 
                onChange={(e, value) => setCurrent(value)} 
                centered 
                textColor="primary" 
                indicatorColor="primary"
            >
                <Tab className={classes.Tab} label="Dotabuff"   component={Link} to="/"/>
                <Tab className={classes.Tab} label="Match"      component={Link} to="/match/"/>
                <Tab className={classes.Tab} label="Player"     component={Link} to="/player/"/>
            </Tabs>
        </AppBar>
        
    )
}

export default Header
